.control-btn .next,
.control-btn .prev{
  position:absolute;
  top: 40%;
  width: 50px;
  line-height: 50px;
  background: #0f3460;
  border-radius: 50%;
  transform: 0.5s;
  z-index: 5;
}
.control-btn .prev{
  left: -20px;
}

.control-btn .next{
  right:  -20px;
}

.control-btn .next i,
.control-btn .prev i{
 font-size: 15px;
 transition: 0.5s;
 color: #fff;
}
.control-btn .next:hover,
.control-btn .prev:hover{
 background: #e94560;

 cursor: pointer;
}